import { MyAxios } from "../helper/myaxios";

export class HomeService {
    static GetListLogVersion(then?: any, exp?: any, final?: any) {
        var url = `logVersions/filter`;
        return MyAxios.Get(url, then, exp, final);
    }

    static GetListMenu(then?: any, exp?: any, final?: any) {
        var url = 'home/renderMenu';
        return MyAxios.Get(url, then, exp, final);
    }
}