import axios, { AxiosRequestConfig } from "axios";
import { Fn } from "./function";

export class MyAxios {
    static AccessToken: string | null = localStorage.getItem('accessToken');
    // static BaseURL: string | undefined = "https://myweb-api-dev.huynq.xyz/api";
    static BaseURL: string | undefined = "https://myweb-api.huynq.xyz/api";
    static SetAccessToken(token: string | null) {
        this.AccessToken = token;
        this.UpdateTokenMyAxios(token);

        if (token === null) {
            localStorage.removeItem("accessToken");
        } else {
            localStorage.setItem("accessToken", token);
        }
    }

    static GetAccessToken() {
        return this.AccessToken;
    }

    // refreshToken
    static RefreshToken: string | null = localStorage.getItem('refreshToken');
    static SetRefreshToken(token: string | null) {
        this.RefreshToken = token;
        if (token === null) {
            localStorage.removeItem("refreshToken");
        } else {
            localStorage.setItem("refreshToken", token);
        }
    };


    static MyAxios = axios.create({
        baseURL: this.BaseURL,
        headers: {
            'Authorization': this.AccessToken !== null ? `Bearer ${this.AccessToken}` : '',
            'Accept-Language': 'vi'
        }
    });

    static UpdateTokenMyAxios(token: string | null) {
        this.MyAxios = axios.create({
            baseURL: this.BaseURL,
            headers: {
                'Authorization': token !== null ? `Bearer ${token}` : '',
                'Accept-Language': 'vi'
            }
        });
    }


    static Get(url: string, then?: any, exp?: any, final?: any, config?: AxiosRequestConfig) {
        return this.MyAxios.get(url, config)
            .then(then)
            .catch((err) => {
                if (exp !== undefined) {
                    exp(err);
                }
                Fn.PrintMsgErr(err);
            })
            .finally(final);;
    }

    static Post(url: string, data?: any, then?: any, exp?: any, final?: any, config?: AxiosRequestConfig) {
        return this.MyAxios.post(url, data, config)
            .then(then)
            .catch((err) => {
                if (exp !== undefined) {
                    exp(err);
                }
                Fn.PrintMsgErr(err);
            })
            .finally(final);;
    }

    static Put(url: string, data?: any, then?: any, exp?: any, final?: any, config?: AxiosRequestConfig) {
        return this.MyAxios
            .put(url, data, config)
            .then(then)
            .catch((err) => {
                if (exp !== undefined) {
                    exp(err);
                }
                Fn.PrintMsgErr(err);
            })
            .finally(final);
    }

    static Delete(url: string, then?: any, exp?: any, final?: any, config?: AxiosRequestConfig) {
        return this.MyAxios
            .delete(url, config)
            .then(then)
            .catch((err) => {
                if (exp !== undefined) {
                    exp(err);
                }
                Fn.PrintMsgErr(err);
            })
            .finally(final);;
    }
}