import { Menu } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { HomeService } from "../../services/homeService";

export default function LoadMenu(props: any) {
    const [menu, setMenu] = useState([]);
    const RenderMenu = useCallback(() => {
        HomeService.GetListMenu()
            .then((data: any) => {
                let menuData = data.data.data.map((data: any, index: number) => {
                    return {
                        key: index,
                        label: data.title,
                        children: data.menu.map((data1: any, index1: number) => {
                            return {
                                label: (
                                    <Link to={data1.route}>{data1.title}</Link>
                                ),
                                key: data1.route
                            }
                        })
                    };
                });
                setMenu(menuData);
            })
            .catch(() => {
            })
    }, [setMenu]);

    useEffect(() => {
        RenderMenu();
    }, [RenderMenu]);

    return (
        <div>
            <Menu items={menu} />
        </div>
    );
}