import { Row, Col } from 'antd';
import { Suspense, useEffect } from 'react';
import {
    BrowserRouter as Router,
    Route,
    Routes,
} from "react-router-dom";
import './layout.css';
// import 'react-pro-sidebar/dist/css/styles.css';
import { Fn } from '../../helper/function';
import LoadMenu from './loadMenu';
import MyRouter from './myRouter';
import { MyAxios } from '../../helper/myaxios';
import { AuthService } from '../../services/authService';
import { LoginResponseModel } from '../../models/authModels';
import routes from '../../router/router';

export default function Layout(props: any) {
    useEffect(() => {
        doRefreshToken(true);
        refreshToken();
    });

    function doRefreshToken(isRemoveToken: boolean) {
        if (MyAxios.AccessToken !== null && MyAxios.RefreshToken !== null) {
            AuthService.RefreshToken((data: any) => {
                new LoginResponseModel(data);
            }, (error: any) => {
                Fn.PrintMsgErr(error);
                if (isRemoveToken) {
                    MyAxios.SetAccessToken(null);
                    MyAxios.SetRefreshToken(null);
                }
            });
        }
    }

    function refreshToken() {
        setInterval(() => {
            doRefreshToken(false);
        }, 20000);
    }

    function RenderComponent(routes: any[]) {
        var data;
        data = routes.map((data: any, index: number) => {
            return (
                <Route
                    key={index}
                    path={data.path}
                    element={data.main}
                />);
        });
        return data;
    }

    return (
        <div className="container-fluid">
            <Router>
                <Row gutter={16}>
                    <Col span={4}>
                        <LoadMenu />
                    </Col>
                    <Col span={20}>
                        <MyRouter />
                        <Suspense fallback={<div>Page is Loading...</div>}>
                            <Routes>
                                {RenderComponent(routes)}
                            </Routes>
                        </Suspense>
                    </Col>
                </Row>
            </Router>
        </div>
    );
}