import { MyAxios } from "../helper/myaxios";
import { LoginModel } from "../models/authModels";

export class AuthService {
    static CheckLogin(loginModel: LoginModel, then?: any, exp?: any, final?: any) {
        let url = "user/login";
        MyAxios.Post(url, loginModel, then, exp, final);
    }

    static RefreshToken = (then?: any, exp?: any, final?: any) => {
        var url = "user/refresh-token";
        var data = {
            refreshToken: MyAxios.RefreshToken
        }

        return MyAxios.Put(url, data, then, exp, final);
    }

    static GetProfile = (then?: any, exp?: any, final?: any) => {
        var url = "user/me";
        return MyAxios.Get(url, then, exp, final);
    }

    static UpdateProfile = (body: any, then?: any, exp?: any, final?: any) => {
        var url = "user/me";
        return MyAxios.Put(url, body, then, exp, final);
    }

    static UpdatePassword = (body: any, then?: any, exp?: any, final?: any) => {
        var url = "user/password";
        return MyAxios.Put(url, body, then, exp, final);
    }

    static RequestForgotPassword = (body: any, then?: any, exp?: any, final?: any) => {
        var url = "user/forgotPassword";
        return MyAxios.Post(url, body, then, exp, final);
    }

    static VerifyForgotPassword = (body: any, then?: any, exp?: any, final?: any) => {
        var url = "user/verifyForgotPassword";
        return MyAxios.Post(url, body, then, exp, final);
    }
}

